.more-news {
	padding: 2% 0 6%; }

.news-list {
	display: flex;
	justify-content: space-between; }

.news-list__item {
	position: relative;
	background: #ffffff;
	box-shadow: 1px 1px 1px rgba(0, 0, 1, 0.14);
	border-radius: 2px;
	width: 19%;
	padding: 17px 17px 21px; }

.news-list__link {
	@include georgia-font;
	font-size: 12px;
	text-decoration: underline;
	color: #042b38;
	cursor: pointer; }

.news-list__img {
	position: relative;
	overflow: hidden;
	padding-bottom: 8.8%; }

.news-list__img--size {
	max-width: 120%;
	width: 120%;
	margin-left: -15px;
	margin-top: -10px; }

.news-list__description {
	padding-top: 5%;
	width: 91%;
	@include georgia-font;
	font-size: 11px;
	font-style: italic;
	color: #757575; }

.news-list__coments {
	position: absolute;
	bottom: 3%;
	right: 7%; }


@include r(930) {
	.news-list__item {
		padding-bottom: 10px; }

	.news-list__link {
		font-size: 11px; }

	.news-list__description {
		width: 100%;
		padding-bottom: 10px; }

	.news-list__coments {
		position: static;
		display: inline-block; } }

@include r(860) {
	.more-news {
		padding-bottom: 2%; }

	.news-list {
		flex-wrap: wrap;
		justify-content: space-around; }

	.news-list__item {
		width: 26%;
		min-width: 150px;
		margin-bottom: 10px; }

	.news-list__img--size {
		max-width: 100%;
		width: 100%;
		margin: 0; } }

@include r(780) {
	.news-list {
		justify-content: space-between; }

	.news-list__item {
		width: 40%; }

	.news-list__link {
		font-size: 15px; } }

@include r(400) {
	.news-list__item {
		width: 100%;
		text-align: center; }

	.news-list__img {
		width: 60%;
		min-width: 200px;
		margin: 0 auto; } }

@include r(350) {
	.news_list__item {
		width: 100%; } }

@include r(320) {
	.news_list__img {
		width: 100%; } }
