html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

body, html {
  min-width: 100%;
  min-height: 100%;
  line-height: 1;
  text-transform: none;
  text-rendering: auto;
  visibility: visible;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-size-adjust: none;
  -webkit-text-size-adjust: none;
}

article, aside, details, figcaption, figure, footer, header, hgroup, main, nav, section, summary {
  display: block;
}

audio, canvas, progress, video {
  display: inline-block;
  vertical-align: baseline;
}

video {
  width: 100%;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

img, fieldset, a img {
  border: none;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  vertical-align: middle;
}

a {
  color: #fff;
  text-decoration: none;
}

a:active, a:hover {
  outline: 0;
  text-decoration: none;
}

input[type="submit"], button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  background: 0;
  border: 0;
}

input, textarea {
  resize: none;
  user-select: text;
}

input:-webkit-autofill, textarea:-webkit-autofill, select:-webkit-autofill {
  background-color: #fff !important;
  color: #000 !important;
}

input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

td, th {
  padding: 0;
}

code, kbd, pre, samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

label {
  cursor: pointer;
}

@font-face {
  font-family: "LucidaStd";
  src: url(../fonts/LucidaStd-Bold.eot);
  src: url(../fonts/LucidaStd-Bold.woff);
  src: url(../fonts/LucidaStd-Bold.ttf);
  font-style: normal;
  font-weight: bold;
}

.news-header {
  display: flex;
  border-radius: 2px;
  background: #cacaca;
  align-items: center;
}

.news-header__title {
  position: relative;
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 400;
  line-height: 22px;
  font-size: 18px;
  color: #f6f6f6;
  padding: 7px 32px 7px 12px;
  text-transform: uppercase;
  background: #ae0002;
}

.news-header__title:before {
  content: '';
  position: absolute;
  left: 100%;
  bottom: 0;
  border: 18px solid transparent;
  border-left: 18px solid #ae0002;
}

.news-header__description {
  margin: 0 auto;
  font-family: 'Georgia';
  line-height: 18px;
  font-weight: 400;
  font-size: 12px;
  color: #757575;
}

.coments {
  background: url("../img/icon/com.png") no-repeat;
  background-position-y: 5px;
  font-family: 'Georgia';
  line-height: 18px;
  font-weight: 400;
  font-size: 11px;
  font-style: italic;
  color: #989898;
  padding-left: 25px;
}

@media only screen and (max-width: 990px) {
  .news-header {
    flex-wrap: wrap;
  }
  .news-header__title {
    padding: 7px;
    font-size: 15px;
  }
}

@media only screen and (max-width: 865px) {
  .news-header__title {
    width: 100%;
  }
  .news-header__title:before {
    display: none;
  }
  .news-header__description {
    width: 100%;
    padding: 7px;
  }
}

@media only screen and (max-width: 400px) {
  .news-header__title {
    font-size: 15px;
  }
}

.page {
  background: url("../img/bg/main-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  padding: 2.5% 0 10%;
}

.wrap {
  margin: 0 auto;
  max-width: 1000px;
  background: #ededed;
}

.container {
  padding: 0 31px;
}

.header {
  background-image: linear-gradient(to top, #e2e2e2 0%, #ffffff 100%);
}

.header-head {
  position: relative;
  padding: 12px 0;
}

.header-head__label {
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 400;
  line-height: 22px;
  background: #85afbd;
  padding: 1px 5px;
  border-radius: 3px;
  font-size: 14px;
  text-transform: uppercase;
  color: #ffffff;
}

.header-head__news {
  padding-left: 6px;
  font-family: 'Georgia';
  line-height: 18px;
  font-weight: 400;
  font-size: 11px;
  font-style: italic;
  color: #757575;
}

.header-head__button {
  position: absolute;
  top: 22%;
  left: 68.4%;
  padding: 10px;
}

.header-head__button:after {
  display: block;
  content: '';
  width: 4px;
  height: 4px;
  border-right: 1px solid #757575;
  border-top: 1px solid #757575;
  transform: rotate(50deg);
}

.header-main {
  background: url("../img/bg/bg-header.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  padding: 2.7% 0 2.3%;
  color: #f6f6f6;
  text-transform: uppercase;
}

.header-logo {
  padding-right: 17px;
}

.header-logo,
.header-navigation {
  display: inline-block;
  vertical-align: top;
}

.header-menu__link {
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 400;
  line-height: 22px;
}

.header-menu__link--is-active, .header-menu--bottom .header-menu__link:hover {
  background: #092f3b;
  border-radius: 3px;
  box-shadow: 1px 1px 0 rgba(255, 255, 255, 0.1), inset 1px 1px 1px rgba(0, 0, 1, 0.28);
}

.header-menu--top .header-menu__link {
  color: #a9a9a9;
  font-size: 14px;
  padding-left: 11px;
}

.header-menu--top .header-menu__link:hover {
  color: #ffffff;
}

.header-menu--bottom {
  padding-top: 6px;
}

.header-menu--bottom .header-menu__link {
  padding: 6px 11px;
  color: #f6f6f6;
  font-size: 18px;
  line-height: 22px;
}

@media only screen and (max-width: 1010px) {
  .page {
    padding-top: 0;
  }
  .wrap {
    max-width: 1010px;
  }
}

@media only screen and (max-width: 905px) {
  .header-navigation {
    padding-left: 0;
  }
  .header-menu--top .header-menu__link {
    padding-left: 8px;
  }
  .header-menu--bottom .header-menu__link {
    padding: 6px 8px;
    font-size: 15px;
  }
}

@media only screen and (max-width: 779px) {
  .header-menu--top .header-menu__link {
    padding-left: 4px;
  }
  .header-menu--bottom .header-menu__link {
    padding: 6px 4px;
  }
  .header-head__button {
    position: static;
  }
}

@media only screen and (max-width: 724px) {
  .header-menu--bottom .header-menu__link {
    position: static;
  }
  .header-main {
    position: relative;
    padding-bottom: 50px;
  }
  .header-menu--top {
    display: inline-block;
  }
  .header-menu--top .header-menu__link {
    display: block;
    padding: 0 5px 7px;
  }
  .header-menu--bottom {
    position: absolute;
    bottom: 8%;
    left: 4%;
  }
}

@media only screen and (max-width: 530px) {
  .header-logo {
    padding-bottom: 10px;
  }
  .header-menu__button {
    position: absolute;
    top: 15px;
    right: 5%;
    border-bottom: 9px double #a6a6a6;
    border-top: 3px solid #a6a6a6;
    height: 15px;
    width: 7%;
  }
  .header-menu--top {
    display: block;
  }
  .header-menu--top .header-menu__link {
    display: inline-block;
    padding: 10px 10px 5px 0;
  }
  .header-main {
    padding-bottom: 15px;
  }
  .header-navigation {
    display: block;
  }
  .header-menu--is-hidden {
    visibility: hidden;
    opacity: 0;
  }
  .header-menu--is-hidden .header-menu__link {
    display: block;
    padding: 4px 0;
    color: white;
  }
  .header-menu--is-visible {
    position: static;
    visibility: visible;
    opacity: 1;
    animation: menu__is-open 0.3s linear;
  }
  .header-menu__link--is-active, .header-menu--bottom .header-menu__link:hover {
    background: transparent;
    box-shadow: none;
  }
}

@media only screen and (max-width: 400px) {
  .wrap {
    padding: 0;
  }
  .header-main {
    padding-top: 20px;
  }
}

@keyframes menu__is-open {
  0% {
    height: 0;
    opacity: 0;
  }
  100% {
    height: 200px;
    opacity: 1;
  }
}

.news {
  display: flex;
  justify-content: space-between;
  padding: 3% 3.1% 2%;
}

.all-news {
  width: 77.2%;
}

.top-news__article {
  background: url("../img/bg/news-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  margin-top: 5px;
  padding: 15% 0 5% 5.5%;
}

.top-news__content {
  width: 43.5%;
  border-radius: 2px;
  background: #1e1e1e;
  padding: 2.5% 3.5% 3% 2.4%;
