  }
}

.more-news {
  padding: 2% 0 6%;
}

.news-list {
  display: flex;
  justify-content: space-between;
}

.news-list__item {
  position: relative;
  background: #ffffff;
  box-shadow: 1px 1px 1px rgba(0, 0, 1, 0.14);
