  font-weight: 400;
  font-size: 12px;
  text-decoration: underline;
  color: #042b38;
  cursor: pointer;
}

.news-list__img {
  position: relative;
  overflow: hidden;
  padding-bottom: 8.8%;
}

.news-list__img--size {
  max-width: 120%;
  width: 120%;
  margin-left: -15px;
  margin-top: -10px;
}

.news-list__description {
  padding-top: 5%;
  width: 91%;
  font-family: 'Georgia';
  line-height: 18px;
  font-weight: 400;
  font-size: 11px;
  font-style: italic;
  color: #757575;
}

.news-list__coments {
  position: absolute;
  bottom: 3%;
  right: 7%;
}

@media only screen and (max-width: 930px) {
  .news-list__item {
    padding-bottom: 10px;
  }
  .news-list__link {
    font-size: 11px;
  }
  .news-list__description {
