    margin-bottom: 10px;
  }
  .news-list__img--size {
    max-width: 100%;
    width: 100%;
    margin: 0;
  }
}

@media only screen and (max-width: 780px) {
  .news-list {
    justify-content: space-between;
  }
  .news-list__item {
    width: 40%;
  }
  .news-list__link {
    font-size: 15px;
  }
}

@media only screen and (max-width: 400px) {
  .news-list__item {
    width: 100%;
    text-align: center;
  }
  .news-list__img {
    width: 60%;
    min-width: 200px;
    margin: 0 auto;
  }
}

@media only screen and (max-width: 350px) {
  .news_list__item {
    width: 100%;
  }
