.news {
	display: flex;
	justify-content: space-between;
	padding: 3% 3.1% 2%; }

.all-news {
	width: 77.2%; }

.top-news__article {
	background: url('../img/bg/news-bg.png');
	@include cover;
	margin-top: 5px;
	padding: 15% 0 5% 5.5%; }

.top-news__content {
	width: 43.5%;
	border-radius: 2px;
	background: #1e1e1e;
	padding: 2.5% 3.5% 3% 2.4%;
	@include georgia-font;
	font-size: 12px;
	text-align: justify;
	color: #e2e2e2; }


@include r(600) {
	.news {
		flex-wrap: wrap; }

	.all-news {
		width: 100%; } }

@include r(500) {
	.news {
		padding: 3% 0% 2%; } }

@include r(400) {
	.top-news__article {
		padding: 25% 0 15% 3%; }

	.top-news__content {
		width: 70%; } }




